'use client';

import { useState } from 'react';
import { type NewProjectUserInput } from '@/services/new_project_agent';

export default function ClaudeTestPage() {
  const [response, setResponse] = useState<string>('');
  const [prompt, setPrompt] = useState<string>('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string>('');

  const testWithSampleData = async () => {
    setLoading(true);
    setError('');
    setResponse('');
    setPrompt('');

    try {
      const res = await fetch('/api/claude/new-project');
      const data = await res.json();

      if (data.success) {
        setResponse(data.response);
        setPrompt(data.filledPrompt);
      } else {
        setError(data.error || 'Unknown error occurred');
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to call API');
    } finally {
      setLoading(false);
    }
  };

  const testWithCustomData = async () => {
    setLoading(true);
    setError('');
    setResponse('');
    setPrompt('');

    const customUserInput: NewProjectUserInput = {
      brand_name: 'Lucky Casino',
      target_country: 'Canada',
      target_language: 'English',
      site_type: 'single-page',
      structure: {
        selectedPages: ['Home'],
        pageSections: {
          'Home': ['Hero Banner', 'Game Gallery', 'Bonuses Section', 'Testimonials', 'Contact Form']
        },
        totalPages: 1,
        structureCompletedAt: new Date().toISOString()
      }
    };

    try {
      const res = await fetch('/api/claude/new-project', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ userInput: customUserInput })
      });
      
      const data = await res.json();

      if (data.success) {
        setResponse(data.response);
        setPrompt(data.filledPrompt);
      } else {
        setError(data.error || 'Unknown error occurred');
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to call API');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 py-12 px-4">
      <div className="max-w-6xl mx-auto">
        <h1 className="text-3xl font-bold text-gray-900 mb-8">
          New Project Agent Test
        </h1>

        <div className="bg-white rounded-lg shadow-md p-6 mb-6">
          <h2 className="text-xl font-semibold mb-4">Test New Project Agent</h2>
          
          <div className="space-y-4">
            <button
              onClick={testWithSampleData}
              disabled={loading}
              className="bg-blue-600 text-white px-6 py-2 rounded hover:bg-blue-700 disabled:bg-blue-400 disabled:cursor-not-allowed"
            >
              {loading ? 'Testing...' : 'Test with Sample Data (Multi-page)'}
            </button>

            <button
              onClick={testWithCustomData}
              disabled={loading}
              className="bg-green-600 text-white px-6 py-2 rounded hover:bg-green-700 disabled:bg-green-400 disabled:cursor-not-allowed ml-4"
            >
              {loading ? 'Testing...' : 'Test with Custom Data (Single-page)'}
            </button>
          </div>
        </div>

        {error && (
          <div className="bg-red-50 border border-red-200 rounded-lg p-4 mb-6">
            <h3 className="text-red-800 font-semibold mb-2">Error</h3>
            <p className="text-red-700">{error}</p>
          </div>
        )}

        {prompt && (
          <div className="bg-blue-50 border border-blue-200 rounded-lg p-4 mb-6">
            <h3 className="text-blue-800 font-semibold mb-2">System Prompt (Filled)</h3>
            <div className="bg-white rounded p-3 border">
              <pre className="whitespace-pre-wrap text-sm text-gray-800 max-h-96 overflow-y-auto">
                {prompt}
              </pre>
            </div>
          </div>
        )}

        {response && (
          <div className="bg-green-50 border border-green-200 rounded-lg p-4">
            <h3 className="text-green-800 font-semibold mb-2">Claude Response</h3>
            <div className="bg-white rounded p-3 border">
              <pre className="whitespace-pre-wrap text-sm text-gray-800 max-h-96 overflow-y-auto">
                {response}
              </pre>
            </div>
          </div>
        )}

        <div className="bg-gray-50 rounded-lg p-4 mt-6">
          <h3 className="font-semibold mb-2">API Endpoints</h3>
          <ul className="text-sm space-y-1">
            <li><strong>GET /api/claude/new-project</strong> - Test with sample data</li>
            <li><strong>POST /api/claude/new-project</strong> - Test with custom userInput</li>
          </ul>
        </div>
      </div>
    </div>
  );
}